.index-page {
    height: 100%;
}

.index-container {
    height: 100%;
    display: grid;
    grid-template-rows: auto 1fr;
    gap: 1rem;
    overflow: hidden;

    .actions {
        display: flex;
        flex-direction: row;

        .filter {
            margin-left: auto;
        }
    }

    .content {
        display: flex;
        flex-direction: row;
        gap: 2.125rem;
        overflow: hidden;
        
        .table-container {
            width: 100%;
            height: 100%;
            display: flex;
            flex-direction: column;
            gap: 1rem;

            .table-title {
                font-size: 1.25rem;
            }

            .table {
                text-align: left;
                width: 100%;
                height: 100%;
                display: flex; 
                flex-direction: column;
                border-radius: 0.375rem;
                border: 1px solid $gray;
                overflow: hidden;

                &[data-number-columns="2"] {
                    tr {
                        grid-template-columns: 1fr 8rem;
                    }
                }

                &[data-number-columns="3"] {
                    tr {
                        grid-template-columns: 1fr 1fr 8rem;
                    }
                }

                &[data-number-columns="4"] {
                    tr {
                        grid-template-columns: 1fr 1fr 1fr 8rem;
                    }
                }

                &[data-number-columns="5"] {
                    tr {
                        grid-template-columns: 1fr 1fr 1fr 1fr 8rem;
                    }
                }

                &[data-number-columns="6"] {
                    tr {
                        grid-template-columns: 1fr 1fr 1fr 1fr 1fr 8rem;
                    }
                }

                &[data-number-columns="7"] {
                    tr {
                        grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 8rem;
                    }
                }

                &[data-number-columns="8"] {
                    tr {
                        grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr 8rem;
                    }
                }

                &[data-number-columns="9"] {
                    tr {
                        grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 8rem;
                    }
                }

                thead {
                    width: 100%;
                    overflow-y: scroll;
                    display: flex; 
                    width: 100%;

                    &::-webkit-scrollbar {
                        background-color: transparent;
                        border-bottom: 1px solid $gray;
                    }

                    tr {
                        border-bottom: 1px solid $gray;
                        width: 100%;
                        display: grid;
                        align-items: center;
                            
                        th {
                            padding: 0.6875rem 1.25rem;
                            vertical-align: middle;
                            height: max-content;
                        }
                    }                    
                }
    
                tbody {
                    width: 100%;
                    height: 100%;
                    overflow-y: scroll;
                    display: flex; 
                    flex-direction: column;
                    
                    tr {
                        border-bottom: 1px solid $gray;
                        width: 100%;
                        display: grid;
                        align-items: center;

                        &::-webkit-scrollbar {
                            background-color: $light-gray;
                        }

                        td {
                            padding: 0.6875rem 1.25rem;
                            vertical-align: middle;
                            height: max-content;
                            font-weight: 600;

                            &:last-of-type {
                                text-align: right;
                            }
                        }
                    }
                }

                .table-button {
                    border: none;
                    background: none;
                    height: 1.5rem;
                    width: auto;
                    fill: $gray;
                    cursor: pointer;
                }

                a {
                    color: $green;
                }
            }
        }   
    }
}